/* You can add global styles to this file, and also import other style files */
/* Breakpoint mixins */
@font-face {
  font-family: Quicksand;
  src: url('Quicksand-Regular.ttf');
}
@font-face {
  font-family: Quicksand-Bold;
  src: url('Quicksand-Bold.ttf');
}
@font-face {
  font-family: Quicksand-Light;
  src: url('Quicksand-Light.ttf');
}
@font-face {
  font-family: Quicksand-SemiBold;
  src: url('Quicksand-SemiBold.ttf');
}
@font-face {
  font-family: Quicksand-Medium;
  src: url('Quicksand-Medium.ttf');
}
@font-face {
  font-family: Dosis-Medium;
  src: url('Dosis-Medium.ttf');
}
@font-face {
  font-family: Dosis-Bold;
  src: url('Dosis-Bold.ttf');
}
@font-face {
  font-family: Dosis-ExtraBold;
  src: url('Dosis-ExtraBold.ttf');
}
@font-face {
  font-family: Material-Design-Iconic-Font;
  src: url('Material-Design-Iconic-Font.ttf');
}
.fs12 {
  font-size: 0.75rem;
}
.fs14 {
  font-size: 0.875rem;
}
.fs18 {
  font-size: 1.125rem;
}
.fs20 {
  font-size: 1.25rem;
}
.fs24 {
  font-size: 1.5rem;
}
.fs28 {
  font-size: 1.75rem;
}
.fs30 {
  font-size: 1.875rem;
}
.center {
  align-content: center;
  justify-items: center;
  margin: auto;
}
.flex {
  display: flex;
}
.flex > *.full {
  flex: 1;
}
.flex.column {
  flex-direction: column;
}
.flex.justify-center {
  justify-content: center;
}
.flex.align-center {
  align-items: center;
}
.flex.space-between {
  justify-content: space-between;
}
.flex.space-around {
  justify-content: space-around;
}
.main-container {
  max-width: 1400px;
  margin: auto;
  transition: max-width 0.5s;
  padding: 0 2rem;
}
@media (max-width: 768px) {
  .main-container {
    padding: 0 1.5rem;
  }
}
@media (max-width: 480px) {
  .main-container {
    padding: 0 0.5rem;
  }
}
@media (min-width: 1400px) {
  .main-container {
    max-width: 100%;
  }
}
@media (max-width: 1000px) {
  .main-layout {
    max-width: 980px;
  }
}
* {
  box-sizing: border-box;
}
::selection {
  background-color: #4faaf8;
}
html {
  font-family: Quicksand, sans-serif;
  line-height: normal;
  font-size: 14px;
  scroll-behavior: smooth;
  color: #333;
  background-color: #fafafa;
  margin: 0;
  padding: 0;
}
@media screen and (max-width: 1130px) {
  html {
    font-size: 13px;
  }
}
body {
  font-family: Quicksand, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #fafafa;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
img {
  width: 100%;
}
p {
  margin: 0;
}
a {
  color: inherit;
  text-decoration: none;
}
a:visited {
  color: inherit;
}
ul {
  margin: 0;
}
li {
  list-style-type: none;
}
button {
  cursor: pointer;
}
button:active {
  color: white;
  border-color: white;
}
textarea {
  outline: none;
  resize: none;
  font-family: Quicksand, sans-serif;
  font-size: 1.1rem;
}
input,
select {
  padding: 5px;
  padding-left: none;
  border: unset;
  background: unset;
  border-bottom: 1px solid black;
}
pre {
  margin: 0;
}
button {
  border: none;
  font-family: Quicksand, sans-serif;
}
.create-btn {
  display: flex;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  color: #fff;
  background-color: #3bd975;
  padding: 4px 10px;
  border-radius: 3px;
  transition: background-color, 0.2s;
}
.create-btn:hover {
  background-color: #50dd84;
}
.submit-btn {
  padding: 4px 20px;
  text-transform: uppercase;
  font-size: 1.5rem;
  font-family: Quicksand-SemiBold;
  background-color: #3bd975;
  color: #fff;
  border-radius: 3px;
  transition: background-color, 0.2s;
}
.submit-btn:hover {
  background-color: #50dd84;
}
.submit-btn:disabled {
  background-color: #3bd9757a;
  cursor: not-allowed;
}
.regular-btn {
  padding: 6px 10px;
  border-radius: 3px;
  background-color: #f0f0f0;
  transition: background-color 0.2s;
  font-size: 0.98rem;
}
.regular-btn:hover {
  background-color: #e3e3e3;
}
